import "./style.scss";

const modes = {
  single: "single",
  range: "range",
};

const _isDateLessThenOther = (date1, date2) => {
  const d1 = new Date(date1).getTime();
  const d2 = new Date(date2).getTime();
  return d1 < d2;
};

const _getYear = (date) => new Date(date).getFullYear();

const _getDaysNumberInMonth = (date) => {
  const _date = new Date(date);
  _date.setMonth(new Date(date).getMonth() + 1);
  _date.setDate(0);
  return _date.getDate();
};

const _getFirstDayOfWeek = (date) => {
  const _date = new Date(date);
  _date.setDate(1);
  const day = _date.getDay();

  if (day === 0) return 6; // day is sunday
  return day - 1;
};

const _getCalendarDays = (date) => {
  const month = new Date(date).getMonth();
  const year = new Date(date).getFullYear();
  const daysNumber = _getDaysNumberInMonth(date);
  const firstDayOfWeek = _getFirstDayOfWeek(date);
  const daysNumberPreviosMonth = _getDaysNumberInMonth(new Date(date).setMonth(month - 1));

  const days = new Array(7 * 6).fill({ day: 0, isShadow: false });

  // fill main calendar
  let day = 1;
  for (let i = firstDayOfWeek; i < (daysNumber + firstDayOfWeek); i += 1) {
    days[i] = { day, date: new Date(year, month, day), isShadow: false };
    day += 1;
  }

  // fill after main calendar
  day = 1;
  for (let i = (daysNumber + firstDayOfWeek); i < (7 * 6); i += 1) {
    days[i] = { day, date: new Date(year, month + 1, day), isShadow: true };
    day += 1;
  }

  // fill before main calendar
  if (firstDayOfWeek !== 0) {
    day = daysNumberPreviosMonth;
    for (let i = firstDayOfWeek - 1; i >= 0; i -= 1) {
      days[i] = { day, date: new Date(year, month - 1, day), isShadow: true };
      day -= 1;
    }
  }

  const isLastRowShadowed = days
    .slice(-7)
    .filter(({ isShadow }) => isShadow === false)
    .length === 0;

  if (isLastRowShadowed) return days.slice(0, -7);

  return days;
};

export default class Calendar {
  constructor({
    classId,
    onOutOfCalendar,
    onSelected,
    onClean,
    mode = "single",
  }) {
    this.classId = classId;
    this.mode = mode;
    this.onSelected = onSelected;
    this.onClean = onClean;
    this.onOutOfCalendar = onOutOfCalendar;

    this.headerDateClass = "js-calendar__header-info";
    this.headerNextMonthBtnClass = "js-calendar__header-btn-right";
    this.headerPreviousMonthBtnClass = "js-calendar__header-btn-left";

    this.dayClass = "js-calendar__day";
    this.btnCleanClass = "js-calendar__action-clean";
    this.btnApplyClass = "js-calendar__action-apply";

    this.dayDisabledClass = "calendar__day_disabled";
    this.dayHiddenClass = "calendar__day_hidden";
    this.daySingleSelectedClass = "calendar__day_select-one";
    this.dayRangeLeftSelectedClass = "calendar__day_select-range-left";
    this.dayRangeRightSelectedClass = "calendar__day_select-range-right";
    this.dayRangeMiddleSelectedClass = "calendar__day_select-range-middle";

    this.monthList = ["январь", "февраль", "март", "апрель",
      "май", "июнь", "июль", "август", "сентябрь",
      "окрябрь", "ноябрь", "декабрь"];

    if (mode !== "single" && mode !== "range") { throw new Error("init Calendar error: param `mode` should be `single` or `range`"); }

    this.state = {
      currentDate: Date.now(),
      singleDate: null,
      range: {
        dateLeft: null,
        dateRight: null,
      },
    };

    this._initDom();

    this._initHeaderEvents();
    this._initCalendarEvents();
    this._initActionEvents();

    this._render();
  }

  _initDom() {
    this.headerDateEl = document.querySelector(`.${this.classId} .${this.headerDateClass}`);
    this.headerNextMonthBtnEl = document.querySelector(`.${this.classId} .${this.headerNextMonthBtnClass}`);
    this.headerPreviousMonthBtnEl = document.querySelector(`.${this.classId} .${this.headerPreviousMonthBtnClass}`);

    this.daysEl = document.querySelectorAll(`.${this.classId} .${this.dayClass}`);

    this.btnCleanEl = document.querySelector(`.${this.classId} .${this.btnCleanClass}`);
    this.btnApplyEl = document.querySelector(`.${this.classId} .${this.btnApplyClass}`);
  }

  _initHeaderEvents() {
    this.headerNextMonthBtnEl.addEventListener(
      "mousedown",
      this._mousedownNextMonthBtnHandler.bind(this),
    );

    this.headerNextMonthBtnEl.addEventListener(
      "keydown",
      this._keydownNextMonthBtnHandler.bind(this),
    );

    this.headerPreviousMonthBtnEl.addEventListener(
      "mousedown",
      this._mousedownPrevMonthBtnHandler.bind(this),
    );

    this.headerPreviousMonthBtnEl.addEventListener(
      "keydown",
      this._keydownPrevMonthBtnHandler.bind(this),
    );
  }

  _initActionEvents() {
    this.btnCleanEl.addEventListener(
      "mousedown",
      this._mousedownCleanBtnHandler.bind(this),
    );

    this.btnCleanEl.addEventListener(
      "keydown",
      this._keydownCleanBtnHandler.bind(this),
    );

    this.btnApplyEl.addEventListener(
      "mousedown",
      this._mousedownApplyBtnHandler.bind(this),
    );

    this.btnApplyEl.addEventListener(
      "keydown",
      this._keydownApplyBtnHandler.bind(this),
    );
  }

  _initCalendarEvents() {
    for (let i = 0; i < this.daysEl.length; i += 1) {
      this.daysEl[i].addEventListener(
        "mousedown",
        this._mousedownCalendarDayHandler.bind(this),
      );
      this.daysEl[i].addEventListener(
        "keydown",
        this._keydownCalendarDayHandler.bind(this),
      );
    }
  }

  _setState(newState) {
    this.state = Object.assign(this.state, newState);

    this._render();
  }

  _render() {
    // render header
    const currentMonth = this._getMonth(this.state.currentDate);
    const currentYear = _getYear(this.state.currentDate);
    this.headerDateEl.innerHTML = `${currentMonth} ${currentYear}`;

    // render calendar days
    const days = _getCalendarDays(this.state.currentDate);

    for (let i = 0; i < days.length; i += 1) {
      this.daysEl[i].innerHTML = `<div>${days[i].day}</div>`;
      this.daysEl[i].classList = ["calendar__day"];
      this.daysEl[i].attributes["data-date"] = days[i].date;
      this.daysEl[i].attributes["data-shadow"] = days[i].isShadow;
      this.daysEl[i].setAttribute("role", "button");
      this.daysEl[i].setAttribute("tabindex", "0");

      if (days[i].isShadow) {
        this.daysEl[i].classList.add(this.dayDisabledClass);
        this.daysEl[i].setAttribute("tabindex", "-1");
      }
    }

    const fiveWeekDays = 5 * 7;
    // if last week days was shadowed hidden them!
    if (days.length === fiveWeekDays) {
      for (let i = 35; i < 42; i += 1) {
        this.daysEl[i].classList.add(this.dayHiddenClass);
      }
    }

    if (this.state.singleDate !== null) {
      days.forEach(({ date }, index) => {
        if (date.toString() === this.state.singleDate) {
          this.daysEl[index]
            .classList.add(this.daySingleSelectedClass);
        }
      });
    }

    const { range } = this.state;
    if (range.dateLeft !== null) {
      days.forEach(({ date, isShadow }, index) => {
        if (date.toString() === range.dateLeft && isShadow === false) {
          this.daysEl[index]
            .classList.add(this.dayRangeLeftSelectedClass);
        }
      });
    }

    if (range.dateRight !== null) {
      days.forEach(({ date, isShadow }, index) => {
        if (date.toString() === range.dateRight && isShadow === false) {
          this.daysEl[index]
            .classList.add(this.dayRangeRightSelectedClass);
        }
      });
    }

    if (
      this.mode === modes.range
      && range.dateLeft !== null
      && range.dateRight !== null
    ) {
      days.forEach(({ date, isShadow }, index) => {
        if (
          _isDateLessThenOther(range.dateLeft, date)
          && _isDateLessThenOther(date, range.dateRight)
          && isShadow === false
        ) {
          this.daysEl[index]
            .classList.add(this.dayRangeMiddleSelectedClass);
        }
      });
    }
  }

  _selectSingleDay(date) {
    this._setState({ singleDate: date });
  }

  _selectRangeDay(selectingDate) {
    const { range } = this.state;

    if (range.dateLeft === null) {
      this._setState({ range: { dateLeft: selectingDate, dateRight: null } });
      return;
    }

    if (range.dateRight === null && _isDateLessThenOther(range.dateLeft, selectingDate)) {
      this._setState({ range: { dateLeft: range.dateLeft, dateRight: selectingDate } });
      return;
    }

    if (_isDateLessThenOther(selectingDate, range.dateLeft)) {
      this._setState({ range: { dateLeft: selectingDate, dateRight: range.dateRight } });
      return;
    }

    if (_isDateLessThenOther(range.dateRight, selectingDate)) {
      this._setState({ range: { dateLeft: range.dateLeft, dateRight: selectingDate } });
      return;
    }

    if (this._isSelectindDateCloserToRightDate(selectingDate)) {
      this._setState({ range: { dateLeft: range.dateLeft, dateRight: selectingDate } });
    } else {
      this._setState({ range: { dateLeft: selectingDate, dateRight: range.dateRight } });
    }
  }

  _isSelectindDateCloserToRightDate(selectingDate) {
    const rightTimestamp = new Date(this.state.range.dateRight).getTime();
    const leftTimestamp = new Date(this.state.range.dateLeft).getTime();
    const selectingDateTimestamp = new Date(selectingDate).getTime();
    return rightTimestamp - selectingDateTimestamp <= selectingDateTimestamp - leftTimestamp;
  }

  _mousedownCalendarDayHandler(e) {
    e.preventDefault();
    e.stopPropagation();

    this._selectDay(e);
  }

  _keydownCalendarDayHandler(e) {
    if (e.code === "Space" || e.code === "Enter") {
      e.preventDefault();
      e.stopPropagation();

      this._selectDay(e);
    }
  }

  _selectDay(e) {
    const isShadow = e.currentTarget.attributes["data-shadow"];
    if (isShadow) return;

    const date = e.currentTarget.attributes["data-date"].toString();

    if (this.mode === "single") {
      this._selectSingleDay(date);
    } else if (this.mode === "range") {
      this._selectRangeDay(date);
    }
  }

  _mousedownPrevMonthBtnHandler(e) {
    e.preventDefault();
    e.stopPropagation();

    this._setPreviousMonth();
  }

  _setPreviousMonth() {
    const currentDate = new Date(this.state.currentDate);
    const prevMonth = new Date(
      currentDate.setMonth(currentDate.getMonth() - 1),
    ).getTime();
    this._setState({ currentDate: prevMonth });
  }

  _keydownPrevMonthBtnHandler(e) {
    if (e.code === "Space" || e.code === "Enter") {
      e.preventDefault();
      e.stopPropagation();

      this._setPreviousMonth();
    }
  }

  _mousedownNextMonthBtnHandler(e) {
    e.preventDefault();
    e.stopPropagation();

    this._setNextMonth();
  }

  _keydownNextMonthBtnHandler(e) {
    if (e.code === "Space" || e.code === "Enter") {
      e.preventDefault();
      e.stopPropagation();

      this._setNextMonth();
    }
  }

  _setNextMonth() {
    const currentDate = new Date(this.state.currentDate);
    const nextMonth = new Date(
      currentDate.setMonth(currentDate.getMonth() + 1),
    ).getTime();

    this._setState({ currentDate: nextMonth });
  }

  _mousedownCleanBtnHandler(e) {
    e.preventDefault();
    e.stopPropagation();

    this._clean();
  }

  _keydownCleanBtnHandler(e) {
    if (e.code === "Space" || e.code === "Enter") {
      e.preventDefault();
      e.stopPropagation();

      this._clean();
    }
  }

  _clean() {
    this._setState({
      singleDate: null,
      range: { dateLeft: null, dateRight: null },
    });

    this.onClean && this.onClean();
  }

  _mousedownApplyBtnHandler(e) {
    e.preventDefault();
    e.stopPropagation();

    this._apply();
  }

  _keydownApplyBtnHandler(e) {
    if (e.code === "Space" || e.code === "Enter") {
      e.preventDefault();
      e.stopPropagation();

      this._apply();
    }

    if (e.code === "Tab") {
      this.onOutOfCalendar && this.onOutOfCalendar();
    }
  }

  _apply() {
    if (this.mode === modes.single && this.state.singleDate !== null) {
      const resultDate = new Date(this.state.singleDate);
      this.onSelected && this.onSelected(resultDate);
    }

    const { range } = this.state;
    if (
      this.mode === modes.range
      && range.dateLeft !== null
      && range.dateRight !== null
    ) {
      this.onSelected && this.onSelected(range);
    }
  }

  _getMonth(date) {
    return this.monthList[new Date(date).getMonth()];
  }
}

export {
  modes,
};
