import "./style.scss";
import "./dropdown_item";

import { pluralize } from "numeralize-ru";

export const types = {
  rooms: "rooms",
  guests: "guests",
};

const pluralizeNouns = {
  гость: ["гость", "гостя", "гостей"],
  младенец: ["младенец", "младенца", "младенцев"],
  спальни: ["спальня", "спальни", "спален"],
  кровати: ["кровать", "кровати", "кроватей"],
  "ванные комнаты": ["ванная комната", "ванных комнаты", "ванных комнат"],
};

const _capitalizeHeaderMessage = (str) => str
  .split(" ")
  .map((chunk) => chunk[0].toUpperCase() + chunk.slice(1, chunk.length))
  .join(" ");

class Dropdown {
  constructor({
    classId, type, defaultMessage, onChange,
  }) {
    if (type === undefined) { throw new Error("you should specify dropdown type: 'rooms' or 'guests'"); }

    this.type = type;
    this.defaultMessage = defaultMessage;
    this.onChange = onChange;

    this.classId = classId;
    this.headerClass = "js-dropdown__header";
    this.itemClass = "js-dropdown-item";
    this.itemContainerClass = "js-dropdown__content-item";
    this.incrBtnClass = "js-dropdown-item__action-incr";
    this.decrBtnClass = "js-dropdown-item__action-decr";
    this.valueClass = "js-dropdown-item__value";
    this.contentActionsClass = "js-dropdown__content-actions";
    this.applyBtnClass = "js-dropdown__content-action-apply";
    this.cleanBtnClass = "js-dropdown__content-action-clean";
    this.dropdownOpenClass = "dropdown_open";
    this.actionDisabledClass = "dropdown-item__action_disabled";
    this.invisibleClass = "invisible";

    this.state = {
      isOpen: false,
      items: [],
    };
  }

  start() {
    const items = this._initDom();
    this._setState({ items });

    this._initHeaderEvents();
    this._initItemsEvents();

    this._render();
  }

  hideDropdown() {
    this._setState({ isOpen: false });
  }

  _initDom() {
    this.dropdownEl = document.querySelector(`.${this.classId}`);

    if (this.dropdownEl === null) { throw new Error(`dropdown with class ${this.classId} not found`); }

    this.headerEl = document.querySelector(`.${this.classId} .${this.headerClass}`);

    const itemEls = [...document.querySelectorAll(`.${this.classId} .${this.itemClass}`)];

    const items = itemEls.map((item) => {
      const name = item.attributes["data-name"].value;
      const max = parseInt(item.attributes["data-max"].value, 10);
      return {
        name,
        max,
        value: 0,
      };
    });

    this.itemEls = itemEls.map((_, index) => {
      const incrBtnEl = document.querySelector(`
        .${this.classId}
        .${this.itemContainerClass}:nth-child(${index + 1})
        .${this.incrBtnClass}`);

      incrBtnEl.setAttribute("data-index", index);

      const decrBtnEl = document.querySelector(`
        .${this.classId}
        .${this.itemContainerClass}:nth-child(${index + 1})
        .${this.decrBtnClass}`);

      decrBtnEl.setAttribute("data-index", index);

      const valueEl = document.querySelector(`
        .${this.classId}
        .${this.itemContainerClass}:nth-child(${index + 1})
        .${this.valueClass}`);

      return { incrBtnEl, decrBtnEl, valueEl };
    });

    const footerActions = document
      .querySelector(`.${this.classId} .${this.contentActionsClass}`);

    if (footerActions !== null) {
      this.cleanBtnEl = document
        .querySelector(`.${this.classId} .${this.cleanBtnClass}`);
      this.applyBtnEl = document
        .querySelector(`.${this.classId} .${this.applyBtnClass}`);

      this._initFooterEvents();
    }

    return items;
  }

  _initHeaderEvents() {
    this.headerEl.addEventListener("mousedown", this._mousedownHeaderHandler.bind(this));
    this.headerEl.addEventListener("keydown", (e) => {
      if (e.code === "Space" || e.code === "Enter") {
        e.preventDefault();
        e.stopPropagation();

        this._setState({ isOpen: !this.state.isOpen });
      }
    });
  }

  _initItemsEvents() {
    this.itemEls.forEach((item) => {
      item.incrBtnEl.addEventListener("mousedown", this._incrBtnMousedownHandler.bind(this));
      item.incrBtnEl.addEventListener("keydown", this._incrKeydownHandler.bind(this));
      item.decrBtnEl.addEventListener("mousedown", this._decrBtnMousedownHandler.bind(this));
      item.decrBtnEl.addEventListener("keydown", this._decrKeydownHandler.bind(this));
    });

    const footerActions = document
      .querySelector(`.${this.classId} .${this.contentActionsClass}`);

    if (footerActions === null) {
      this.itemEls[this.itemEls.length - 1].incrBtnEl.addEventListener(
        "keydown",
        this._keydownLastItemTabHandler.bind(this),
      );
    }
  }

  _initFooterEvents() {
    this.cleanBtnEl.addEventListener(
      "mousedown",
      this._mousedownCleanBtnHandler.bind(this),
    );

    this.cleanBtnEl.addEventListener(
      "keydown",
      this._keydownCleanHandler.bind(this),
    );

    this.applyBtnEl.addEventListener(
      "mousedown",
      this._mousedownApplyBtnHandler.bind(this),
    );

    this.applyBtnEl.addEventListener(
      "keydown",
      this._keydownApplyHandler.bind(this),
    );

    this.applyBtnEl.addEventListener(
      "keydown",
      this._keydownLastItemTabHandler.bind(this),
    );
  }

  _setState(newState) {
    this.state = Object.assign(this.state, newState);
    this.onChange && this.onChange(this.state.items);
    this._render();
  }

  _render() {
    if (this.state.isOpen) {
      this.dropdownEl.classList.add(this.dropdownOpenClass);
    } else {
      this.dropdownEl.classList.remove(this.dropdownOpenClass);
    }

    this.state.items.forEach((item, index) => {
      const { valueEl } = this.itemEls[index];
      const { incrBtnEl } = this.itemEls[index];
      const { decrBtnEl } = this.itemEls[index];

      valueEl.innerHTML = item.value;

      incrBtnEl.classList.remove(this.actionDisabledClass);
      decrBtnEl.classList.remove(this.actionDisabledClass);

      if (item.value <= 0) {
        decrBtnEl.classList.add(this.actionDisabledClass);
      }

      if (item.value >= item.max) {
        incrBtnEl.classList.add(this.actionDisabledClass);
      }
    });

    if (this.contentActionCleanEl && this._isItemsClean()) {
      this.contentActionCleanEl.classList.add(this.invisibleClass);
    }

    if (this.contentActionCleanEl && !this._isItemsClean()) {
      this.contentActionCleanEl.classList.remove(this.invisibleClass);
    }

    if (!this._isItemsClean()) {
      this.headerEl.innerHTML = this._buildHeaderMessage();
    } else {
      this.headerEl.innerHTML = _capitalizeHeaderMessage(this.defaultMessage);
    }
  }

  _incrBtnMousedownHandler(e) {
    e.preventDefault();
    e.stopPropagation();

    this._incrementItem(e);
  }

  _incrKeydownHandler(e) {
    if (e.code === "Space" || e.code === "Enter") {
      e.preventDefault();
      e.stopPropagation();

      this._incrementItem(e);
    }
  }

  _incrementItem(e) {
    const index = parseInt(e.currentTarget.attributes["data-index"].value, 10);
    const { items } = this.state;

    if (items[index].value < items[index].max) {
      items[index].value += 1;

      this._setState({ items });
    }
  }

  _decrBtnMousedownHandler(e) {
    e.preventDefault();
    e.stopPropagation();

    this._decrementItem(e);
  }

  _decrKeydownHandler(e) {
    if (e.code === "Space" || e.code === "Enter") {
      e.preventDefault();
      e.stopPropagation();

      this._decrementItem(e);
    }
  }

  _decrementItem(e) {
    const index = parseInt(e.currentTarget.attributes["data-index"].value, 10);
    const { items } = this.state;

    if (items[index].value > 0) {
      items[index].value -= 1;

      this._setState({ items });
    }
  }

  _keydownLastItemTabHandler(e) {
    if (e.code === "Tab") {
      e.preventDefault();
      e.stopPropagation();

      this.hideDropdown();
    }
  }

  _mousedownHeaderHandler(e) {
    e.preventDefault();
    e.stopPropagation();

    this._setState({ isOpen: !this.state.isOpen });
  }

  _mousedownCleanBtnHandler(e) {
    e.preventDefault();
    e.stopPropagation();

    this._cleanItems();
  }

  _keydownCleanHandler(e) {
    if (e.code === "Space" || e.code === "Enter") {
      e.preventDefault();
      e.stopPropagation();

      this._cleanItems();
    }
  }

  _mousedownApplyBtnHandler(e) {
    e.preventDefault();
    e.stopPropagation();

    this._applyDropdown();
  }

  _keydownApplyHandler(e) {
    if (e.code === "Space" || e.code === "Enter") {
      e.preventDefault();
      e.stopPropagation();

      this._applyItems();
    }
  }

  _applyDropdown() {
    this._setState({ isOpen: false });
  }

  _cleanItems() {
    const items = this.state.items.map((item) => ({
      ...item,
      value: 0,
    }));

    this._setState({ items });
  }

  _isItemsClean() {
    return this.state.items.filter(({ value }) => value !== 0).length === 0;
  }

  _buildHeaderMessage() {
    const { items } = this.state;

    if (this.type === types.rooms) {
      const message = items.map((item) => {
        const noun = pluralize(item.value, ...pluralizeNouns[item.name]);
        if (item.value > 0) return `${item.value} ${noun}, `;
        return "";
      }).reduce((acc, data) => acc + data, "").slice(0, -2);

      return message;
    }

    if (this.type === types.guests) {
      let guests = 0;
      let toddlers = 0;

      items.forEach((item) => {
        if (item.name === "младенцы") {
          toddlers += item.value;
        } else {
          guests += item.value;
        }
      });

      const guestsNoun = pluralize(guests, ...pluralizeNouns["гость"]);
      if (toddlers > 0) {
        const toddlersNoun = pluralize(toddlers, ...pluralizeNouns["младенец"]);
        return `${guests} ${guestsNoun}, ${toddlers} ${toddlersNoun}`;
      }

      return `${guests} ${guestsNoun}`;
    }

    return this.defaultMessage;
  }
}

export default Dropdown;
