import "../buttons";
import "../top_bar_user";

import "./style.scss";

function topBarInit() {
  const switchButton = document.querySelector(".js-top-bar__switch-button");
  const topBar = document.querySelector(".js-top-bar");

  const _toggleMenuHandler = (e) => {
    e.preventDefault();
    e.stopPropagation();

    topBar.classList.toggle("top-bar_close");
  };

  switchButton.addEventListener("click", _toggleMenuHandler);

  topBar.classList.toggle("top-bar_close");
}

topBarInit();
