import Calendar, { modes as calendarModes } from "../calendar/index";

import "../text_field";
import "./style.scss";

export const modes = {
  single: "single", // single text field
  double: "double", // double text field
};

const monthsRus = [
  "янв",
  "фев",
  "мар",
  "апр",
  "май",
  "июн",
  "июл",
  "авг",
  "сен",
  "окт",
  "ноя",
  "дек",
];

class DateRange {
  constructor({
    classId,
    onCalendarOpen,
    onSelected,
    onClean,
    mode = modes.double,
  }) {
    this.classId = classId;
    this.mode = mode;

    this.onCalendarOpen = onCalendarOpen;
    this.onSelected = onSelected;
    this.onClean = onClean;

    this.inputContainerClass = "js-date-range__inputs";
    this.classCalendarOpen = "date-range_calendar-open";

    this.calendar = new Calendar({
      classId: `${classId}-calendar`,
      mode: calendarModes.range,
      onSelected: this._applyCalendarRange.bind(this),
      onClean: this._cleanCalendar.bind(this),
      onOutOfCalendar: this._onOutOfCalendar.bind(this),
    });

    this.state = {
      isCalendarOpen: false,
      dateLeft: null,
      dateRight: null,
    };
  }

  start() {
    this._initDom();
    this._initEvents();
    this._render();
  }

  _initDom() {
    this.rootEl = document.querySelector(`.${this.classId}`);
    if (this.rootEl === null) throw new Error(`DataRange error: Elent with id .${this.classId} not found`);

    const inputs = document.querySelectorAll(`.${this.classId} .${this.inputContainerClass} input`);

    if (this.mode === modes.double && inputs.length !== 2) {
      throw new Error("DateRange error: not found rightDateInputEl in double mode");
    }

    if (this.mode === modes.double) {
      const [leftDateInputEl, rightDateInputEl] = inputs;
      this.leftDateInputEl = leftDateInputEl;
      this.rightDateInputEl = rightDateInputEl;
      return;
    }

    const [leftDateInputEl] = inputs;
    this.leftDateInputEl = leftDateInputEl;
  }

  _initEvents() {
    this.leftDateInputEl.addEventListener(
      "focus",
      this._inputFocusHandler.bind(this),
    );

    if (this.mode === modes.double) {
      this.rightDateInputEl.addEventListener(
        "focus",
        this._inputFocusHandler.bind(this),
      );
    }
  }

  _setState(newState) {
    if (newState.isCalendarOpen === true) {
      this.onCalendarOpen && this.onCalendarOpen();
    }

    this.state = Object.assign(this.state, newState);
    this._render();
  }

  _render() {
    if (this.state.isCalendarOpen) {
      this.rootEl.classList.add("date-range_calendar-open");
    } else {
      this.rootEl.classList.remove("date-range_calendar-open");
    }

    if (this.state.dateLeft !== null) {
      const date = new Date(this.state.dateLeft);
      const value = this.mode === modes.single
        ? `${date.getDate()} ${monthsRus[date.getMonth()]}`
        : `${date.getDate()}.${date.getMonth()}.${date.getFullYear()}`;

      this.leftDateInputEl.value = value;
    }

    if (this.state.dateLeft === null) {
      this.leftDateInputEl.value = "";
    }

    if (this.state.dateRight !== null) {
      const dateLeft = new Date(this.state.dateLeft);
      const dateRight = new Date(this.state.dateRight);
      if (this.mode === modes.double) {
        this.rightDateInputEl.value = `${dateRight.getDate()}.${dateRight.getMonth()}.${dateRight.getFullYear()}`;
      } else {
        this.leftDateInputEl.value = `${dateLeft.getDate()} ${monthsRus[dateLeft.getMonth()]} - ${dateRight.getDate()} ${monthsRus[dateRight.getMonth()]}`;
      }
    }

    if (this.state.dateRight === null && this.mode === modes.double) {
      this.rightDateInputEl.value = "";
    }
  }

  _inputFocusHandler(e) {
    e.preventDefault();
    e.stopPropagation();

    this._showCalendar();
  }

  _appendHideCalendarEvents() {
    const layout = document.createElement("div");
    layout.classList.add("js-calendar-event-layout");
    layout.setAttribute("style", "width: 100%; height: 100%; z-index: 1; position: fixed; top: 0;");

    layout.addEventListener("mousedown", this._hideCalendar.bind(this));

    document.body.prepend(layout);
  }

  _cleanHideCalendarEvents() {
    const layout = document.querySelector(".js-calendar-event-layout");
    layout.removeEventListener("mousedown", this._hideCalendar.bind(this));
    layout.remove();
  }

  _cleanCalendar() {
    this._setState({
      dateLeft: null,
      dateRight: null,
    });
    this.onClean && this.onClean();
  }

  _onOutOfCalendar() {
    this._hideCalendar();
  }

  _showCalendar() {
    if (this.state.isCalendarOpen) return;

    this._appendHideCalendarEvents();
    this._setState({ isCalendarOpen: true });
  }

  _hideCalendar() {
    this._cleanHideCalendarEvents();
    this._setState({ isCalendarOpen: false });
  }

  _applyCalendarRange({ dateLeft, dateRight }) {
    this._setState({ dateLeft, dateRight });
    this.onSelected && this.onSelected({ dateLeft, dateRight });
    this._hideCalendar();
  }
}

export default DateRange;
