import DateRange from "../date_range";
import Dropdown, { types } from "../dropdown";

import "../buttons";
import "./style.scss";

export default function roomSelectorFormInit() {
  const formEl = document.querySelector(".js-room-selector-form");

  const submitFormHandler = (e) => {
    e.preventDefault();
    e.stopPropagation();

    /* eslint-disable no-console */
    console.log("submit form");
  };

  formEl.addEventListener("submit", submitFormHandler);

  const dropdown = new Dropdown({
    classId: "js-dropdown-room-selector",
    type: types.guests,
    defaultMessage: "Сколько гостей",
  });
  dropdown.start();

  const calendarOpenHandler = () => {
    dropdown.hideDropdown();
  };

  const dateRange = new DateRange({
    classId: "js-date-range-room-selector",
    onCalendarOpen: calendarOpenHandler,
  });
  dateRange.start();
}
